import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from "../views/index.vue"



Vue.use(VueRouter)



  const routes = [
  {
    path: '/',
    name: 'index',
    redirect:"/home",
    component: Index,
    children:[
      { path:"/address", name:"address", component:()=>import("../views/Address.vue")},
      { path:"/city", name:"city", component:()=>import("../views/City.vue")},
      { path:"/login", name:"login", component:()=>import("../views/login.vue")},
      { path:"/order", name:"order", component:()=>import("../views/Order.vue")},
      { path:"/store", name:"store", component:()=>import("../views/Address.vue")},
      { path:"/me", name:"me", component: ()=>import("../views/Me.vue")},
      { path:"/home",  name:"home", component: ()=>import("../views/Home.vue")},
      { path:"/search",component:()=>import("../views/Search.vue"),name:"search"}
     
    ]
  },
  
]

const router = new VueRouter({
  routes
})

